/* cookie-consent banner */
#cookie-consent-section {
    display: none;
    margin: 0;
    font-size: 13px;
    line-height: 15px;
    padding-top: 1px;
    float: none;
    background: #00a0cf none;
    width: 100%;
    height: auto;
    color: #fff;
    clear: both;
    font-family: 'Segoe UI',Tahoma,Helvetica,Sans-Serif;
}

#cookie-consent-section * {
    font-family: 'Segoe UI',Tahoma,Helvetica,Sans-Serif;
}

#cookie-consent-section div.row {
    clear: both;
    margin: 0 auto;
    max-width: 1180px;
    width: 97%;
    clear: both;
}

#cookie-consent-section a {
    text-decoration: none;
    color: #fff;
}

#cookie-consent-section div.left {
    background-color: #00a0cf;
    float: left;
    padding-left: 0;
    padding-right: 12px;
    margin-top: 1px;
    margin-bottom: 1px;
    white-space: normal;
    box-sizing: border-box;
}

#cookie-consent-section div.middle {
    margin: -1px 0 0;
    padding: 2px 10px 2px 10px;
    float: left;
    background-color: #015c77;
    cursor: pointer;
}


#cookie-consent-section div.right a {
    float: right;
    padding-top: 3px;
    display: block;
    cursor: pointer;
    background: url('data:image/png;base64,') no-repeat center;
    width: 15px;
    height: 16px;
}


@media screen and (max-width: 1262px) {
    #cookie-consent-section div.left {
        width: 70%;
    }
}

@media screen and (max-width: 644px) {
    #cookie-consent-section div.row {
        width: 80%;
    }
    #cookie-consent-section  div.left {
        width: 40%;
        padding-left: 0;
    }
    #cookie-consent-section div.middle {
        max-width: 90px;
    }
}

#footer #impressum-section {
    display: none;
}